.NavItem__NavItem__3mWhV { /*mobile SideDrawer <li> */
  margin: 0px;
  padding: 0px;
  margin-bottom: 10px;
}

.NavItem__NavItem__3mWhV a { /*mobile SideDrawer <a> */
  display: block;
  padding: 10px;
  color: black;
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
}

.NavItem__NavItem__3mWhV a.NavItem__active__3j8z2 {
  background-color: rgba(211, 211, 211, 0.2);
  border-radius: 10px;
  color: red;
}

.NavItem__NavItem__3mWhV a:before { 
  content: "";
  display: inline-block;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: 30px 30px;
  width: 30px;
  height: 30px;
  margin-right: 30px;
  opacity: 0.8;
  vertical-align: middle;
}

.NavItem__NavItem__3mWhV:nth-child(2) a:before { /*:nth-child(1) is logo */ 
  background-image: url(data:image/png;base64,);
}

.NavItem__NavItem__3mWhV:nth-child(3) a:before {
  background-image: url(data:image/png;base64,);
}

.NavItem__NavItem__3mWhV:nth-child(4) a:before {
  background-image: url(data:image/png;base64,);
}

.NavItem__NavItem__3mWhV:nth-child(4) {
  margin-top: 80px;
  border-top: 1px solid black;
  padding-top: 30px;
}

@media (min-width: 480px) { /*desktop <li> */
  .NavItem__NavItem__3mWhV {   
    margin: 0px;
    padding: 0px;
    width: 140px;
    margin-right: 12px;
    border: none;
  }

  .NavItem__NavItem__3mWhV:nth-child(4) {
    margin: 0px;
    padding: 0px;
    border: none;
  }

  .NavItem__NavItem__3mWhV a {
    /* for desktop */
    text-align: center; 
    font-size: 22px;
    padding: 10px;
    color: white;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
  }

  .NavItem__NavItem__3mWhV a:hover { /* for desktop */ 
    color: rgba(255, 255, 255, 0.6);
  }

  .NavItem__NavItem__3mWhV a.NavItem__active__3j8z2 { /* for desktop */ 
    background-color: transparent;
    color: white;
    border-bottom: 3px solid red;
    border-radius: 3px;
  }

  .NavItem__NavItem__3mWhV a:before {
    display: none;
  }
}